:host {
  --pfe-tabs--main:         transparent;
  --pfe-tabs--aux:          var(--pfe-theme--color--ui-disabled--text, #797979);
  --pfe-tabs--link:         var(--pfe-theme--color--surface--lightest--link, #06c);
  --pfe-tabs--focus:        var(--pfe-theme--color--surface--lightest--link--focus, #003366);
  --pfe-tabs--highlight:    var(--pfe-theme--color--ui-accent, #fe460d);
  --pfe-tabs__tab--PaddingTop:    calc(var(--pfe-theme--container-padding, 16px) * .666);
  --pfe-tabs__tab--PaddingRight:  calc(var(--pfe-theme--container-padding, 16px) * 1.5);
  --pfe-tabs__tab--PaddingBottom: calc(var(--pfe-theme--container-padding, 16px) * .666);
  --pfe-tabs__tab--PaddingLeft:   calc(var(--pfe-theme--container-padding, 16px) * 1.5);
  position: relative;
  display: block;
  margin: var(--pfe-tabs__tab--Margin, 0 0 -1px);
  padding: var(--pfe-tabs__tab--PaddingTop) var(--pfe-tabs__tab--PaddingRight) var(--pfe-tabs__tab--PaddingBottom) var(--pfe-tabs__tab--PaddingLeft);
  border-top: var(--pfe-tabs__tab--BorderTop, var(--pfe-tabs__tab--BorderWidth, var(--pfe-theme--ui--border-width, 1px)) var(--pfe-theme--ui--border-style, solid) var(--pfe-tabs__tab--BorderColor, transparent));
  border-right: var(--pfe-tabs__tab--BorderRight, var(--pfe-tabs__tab--BorderWidth, var(--pfe-theme--ui--border-width, 1px)) var(--pfe-theme--ui--border-style, solid) var(--pfe-tabs__tab--BorderColor, transparent));
  border-bottom: var(--pfe-tabs__tab--BorderBottom, 0);
  border-left: var(--pfe-tabs__tab--BorderLeft, var(--pfe-tabs__tab--BorderWidth, var(--pfe-theme--ui--border-width, 1px)) var(--pfe-theme--ui--border-style, solid) var(--pfe-tabs__tab--BorderColor, transparent));
  background-color: var(--pfe-tabs--main);
  color: var(--pfe-tabs--aux);
  text-transform: var(--pfe-tabs__tab--TextTransform, none);
  font-weight: var(--pfe-theme--font-weight--normal, 500);
  cursor: pointer;
  text-align: var(--pfe-tabs__tab--TextAlign, center);
}

:host(:focus),
:host(:focus-visible) {
  outline: 1px solid #003366;
  outline: var(--pfe-theme--ui--focus-outline-width, 1px) var(--pfe-theme--ui--focus-outline-style, solid) var(--pfe-tabs--focus);
}

:host([aria-selected="true"]) {
  --pfe-tabs--aux: #131313;
  --pfe-tabs__tab--BorderColor: transparent;
  --pfe-tabs__tab--BorderBottom: var(--pfe-theme--surface--border-width--heavy, 4px) var(--pfe-theme--surface--border-style, solid) var(--pfe-tabs--highlight);
}

:host(:hover) {
  --pfe-tabs--aux: var(--pfe-theme--color--text, #333);
}

@media screen and (min-width: 768px) {
  :host([vertical]) {
    --pfe-tabs__tab--BorderTop: var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) transparent;
    --pfe-tabs__tab--BorderRight: 0;
    --pfe-tabs__tab--BorderBottom: var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) transparent;
    --pfe-tabs__tab--BorderLeft: var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) transparent;
    --pfe-tabs__tab--Margin: 0 -1px 0 0;
    --pfe-tabs__tab--PadingLeft: calc(var(--pfe-theme--container-padding, 16px) * .75);
    --pfe-tabs__tab--TextAlign: left;
  }
  :host([vertical][aria-selected="true"]) {
    --pfe-tabs__tab--BorderTopColor: transparent;
    --pfe-tabs__tab--BorderRight: var(--pfe-theme--surface--border-width--heavy, 4px) var(--pfe-theme--surface--border-style, solid) var(--pfe-tabs--highlight);
  }
}

:host([aria-selected="true"][on="dark"]),
:host([on="dark"]:hover),
:host([pfe-variant="wind"][aria-selected="true"][on="dark"]),
:host([pfe-variant="wind"][on="dark"]:hover) {
  --pfe-tabs--aux: var(--pfe-theme--color--text--on-dark, #fff);
}

:host([pfe-variant="earth"][aria-selected="false"]) {
  --pfe-tabs--main: var(--pfe-theme--color--surface--lighter, #ececec);
  --pfe-tabs__tab--BorderLeft:   var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--border, #d2d2d2);
  --pfe-tabs__tab--BorderBottom: var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--border, #d2d2d2);
  --pfe-tabs__tab--BorderTop:    var(--pfe-theme--surface--border-width--heavy, 4px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--lighter, #ececec);
}

:host([pfe-variant="earth"][aria-selected="true"]) {
  --pfe-tabs--main: var(--pfe-theme--color--surface--lightest, #fff);
  --pfe-tabs--aux: var(--pfe-theme--color--surface--lightest--text, #333);
  --pfe-tabs__tab--BorderTop:    var(--pfe-theme--surface--border-width--heavy, 4px) var(--pfe-theme--surface--border-style, solid) var(--pfe-tabs--highlight);
  --pfe-tabs__tab--BorderBottom: 0;
  --pfe-tabs__tab--BorderLeft:   var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--border, #d2d2d2);
}

:host([pfe-variant="earth"][aria-selected="true"]:last-of-type) {
  --pfe-tabs__tab--BorderRight: var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--border, #d2d2d2);
}

@media screen and (min-width: 768px) {
  :host([vertical][pfe-variant="earth"]) {
    --pfe-tabs__tab--BorderTop: var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--border, #d2d2d2);
  }
  :host([vertical][pfe-variant="earth"]:first-of-type) {
    --pfe-tabs__tab--BorderTop: 0;
  }
  :host([vertical][pfe-variant="earth"][aria-selected="true"]) {
    --pfe-tabs__tab--BorderTop:   var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--border, #d2d2d2);
    --pfe-tabs__tab--BorderRight: 0;
    --pfe-tabs__tab--BorderLeft:  var(--pfe-theme--surface--border-width--heavy, 4px) var(--pfe-theme--surface--border-style, solid) var(--pfe-tabs--highlight);
  }
  :host([vertical][pfe-variant="earth"][aria-selected="false"]) {
    --pfe-tabs__tab--BorderRight:  var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--border, #d2d2d2);
    --pfe-tabs__tab--BorderBottom: 0;
    --pfe-tabs__tab--BorderLeft:   var(--pfe-theme--surface--border-width--heavy, 4px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--lighter, #ececec);
  }
  :host([vertical][pfe-variant="earth"][aria-selected="true"]:last-of-type) {
    --pfe-tabs__tab--BorderBottom: var(--pfe-theme--ui--border-width, 1px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--surface--border, #d2d2d2);
  }
}
